            height: "18%",
            borderWidth: 1,
            borderColor:['#EAEAEF','#EAEAEF','#EAEAEF','#EAEAEF']
          },
        ],
        tooltip: {
          trigger: "axis",
          // formattertooltip
          formatter: function (params) {
            if (params) {
              var htmlStr = "";
              htmlStr += params[0].name.replace(/\-/g, "/") + "<br/>"; //x
              for (var i = 0; i < params.length; i++) {
                var param = params[i]; // item
                var seriesName = param.seriesName; //
                var value = param.value; //y
                var color = param.color; //
                htmlStr += "<div>";
                htmlStr +=
                  '<span style="margin-right:5px;display:inline-block;width:10px;height:10px;border-radius:5px;background-color:' +
                  color +
                  ';"></span>';
                //
                htmlStr += seriesName + "" + value;
                switch (seriesName) {
                  case "":
                    htmlStr += " " + "";
                    break;
                  // case "":
                  //   htmlStr += " " + "%";
                  //   break;
                  case "":
                    htmlStr += " " + "";
                    break;
                  default:
                    htmlStr += " ";
                }
                htmlStr += "</div>";
              }
              return htmlStr;
            } else {
              return;
            }
          },
          backgroundColor: "#ccc",
          borderWidth: 1,
          borderColor: "#ccc",
          padding: 10,
          textStyle: {
            color: "#000",
            fontSize: 12,
            align: "left",
          },
