            axisLabel: {
              fontSize: 10,
            },
            axisTick: {
              alignWithLabel: false,
              show:false // 
            },
            splitLine: {
              show: false,
            },
            data: this.xData, //x
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "",
            nameLocation: "center",
            nameRotate: 360,
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12,
              fontWeight: "500",
            },
            axisLine: {
              show: true, // 
            },
            axisLabel: {
              show: false, // 
            },
            splitLine:{
              show:true // 
            },
            axisTick:{
              alignWithLabel: false,
              show:false // 
            },
            scale: false,
            boundaryGap: [0, "100%"],
            splitLine: {
              show: false,
            },
            data: this.yData
          },
          {
            type: "value",
            name: "",
            nameLocation: "center",
            nameRotate: 360,
            gridIndex: 1,
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12,
            },
            axisLine: {
              show: true, // 
            },
            axisTick:{
              alignWithLabel: false,
              show:false // 
            },
            axisLabel: {
              show: false, // 
            },
            splitLine:{
              show:true // 
            },
            scale: false,
            boundaryGap: [0, "100%"],
            splitLine: {
              show: false,
            },
            data: this.yData
          },
          {
            type: "value",
            name: "BPO",
            nameLocation: "center",
            nameRotate: 360,
            gridIndex: 2,
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12,
            },
            axisLine: {
              show: true, // 
            },
            axisTick:{
              alignWithLabel: false,
              show:false // 
            },
            axisLabel: {
              show: false, // 
            },
            splitLine:{
