              show:false // 
            },
            axisLabel: {
              show: false, // 
            },
            splitLine:{
              show:true // 
            },
            scale: false,
            boundaryGap: [0, "100%"],
            splitLine: {
              show: false,
            },
            data: this.yData
          },
          {
            type: "value",
            name: "ITO",
            nameLocation: "center",
            nameRotate: 360,
            gridIndex: 4,
            nameGap: 20,
            nameTextStyle: {
              fontSize: 12,
            },
            axisLine: {
              show: true, // 
            },
            axisTick:{
              alignWithLabel: false,
              show:false // 
            },
            axisLabel: {
              show: false, // 
            },
            splitLine:{
              show:true // 
            },
            scale: false,
            boundaryGap: [0, "100%"],
            splitLine: {
              show: false,
            },
            data: this.yData
          }
        ],
        series: [
          {
            name: "",
            type: "line",
            xAxisIndex: 0,
            yAxisIndex: 0,
            hoverAnimation: true, // 
            data: this.y1Data, //
          },
          {
            name: "",
            type: "line",
            xAxisIndex: 1,
            yAxisIndex: 1,
            hoverAnimation: true, // 
            data: this.y2Data, //
          },
          {
            name: "",
            type: "line",
            xAxisIndex: 2,
            yAxisIndex: 2,
            hoverAnimation: true, // 
            data: this.y2Data, //
          },
          {
            name: "",
            type: "line",
            xAxisIndex: 3,
            yAxisIndex: 3,
            hoverAnimation: true, // 
            data: this.y2Data, //
          },
          {
            name: "",
            type: "line",
            xAxisIndex: 4,
            yAxisIndex: 4,
            hoverAnimation: true, // 
            data: this.y2Data, //
          }
        ],
      }
      )
    }
  }
}
</script>
