<template>
  <div>
    <el-button v-if="!listenStatus" @click="startListening()"></el-button>
    <el-button v-else @click="stopListening()"></el-button>
    <div class="item">
      <span v-if="listenStatus" class="listen-status">...</span>
    </div>
    <div class="item">
      <span class="title">{{ tips }}</span>
    </div>
    <div class="item">
      <span class="title"></span><span>{{translateDesc}}</span>
    </div>
  </div>
</template>
<script>
export default {
  data(){
    return {
      recognition: null,
      translateDesc: '',
      listenStatus: false,
      tips:''
    }
  },
  created(){
    console.log('===window===', window)
  },
  methods:{
    startListening() {
      //  Speech Recognition API
      if ('webkitSpeechRecognition' in window) {
        this.recognition = new window.webkitSpeechRecognition()
        this.recognition.continuous = true; // 
        this.recognition.lang = 'zh-CN'; // 

        this.recognition.start(); // 
        console.log('======')
        this.listenStatus = true
        let that = this
        this.recognition.onresult = function(event) {
          var current = event.resultIndex;
          if(event.results[current][0].transcript){
            if(that.translateDesc){
              that.translateDesc = that.translateDesc + ',' + event.results[current][0].transcript;
            }else{
              that.translateDesc = event.results[current][0].transcript;
            }
          }else{
            that.translateDesc = that.translateDesc
          }
          console.log(': ', that.translateDesc);
          that.tips = ""
        };
        this.recognition.onspeechend = function() {
          that.recognition.stop();
        }

        this.recognition.onnomatch = function(event) {
          that.tips = "I didn't recognise that color.";
        }

        this.recognition.onerror = function(event) {
          that.tips = 'Error occurred in recognition: ' + event.error;
        }
      } else {
        console.log(' Speech Recognition API');
      }
    },
    stopListening(){
      this.listenStatus = false
      this.recognition.stop(); // 
    }
  }
}
</script>
<style scoped lang="scss">
.item{
  margin-top: 20px;
  .title{
    color: red;
  }
  .listen-status{
    color: red;
  }
}
</style>