<template>
  <div class="cropper-content">
    <div class="cropper-box">
      <div class="cropper">
        <vue-cropper
            ref="cropper"
            :img="option.img"
            :outputSize="option.outputSize"
            :outputType="option.outputType"
            :info="option.info"
            :canScale="option.canScale"
            :autoCrop="option.autoCrop"
            :autoCropWidth="option.autoCropWidth"
            :autoCropHeight="option.autoCropHeight"
            :fixed="option.fixed"
            :fixedNumber="option.fixedNumber"
            :full="option.full"
            :fixedBox="option.fixedBox"
            :canMove="option.canMove"
            :canMoveBox="option.canMoveBox"
            :original="option.original"
            :centerBox="option.centerBox"
            :height="option.height"
            :infoTrue="option.infoTrue"
            :maxImgSize="option.maxImgSize"
            :enlarge="option.enlarge"
            :mode="option.mode"
            @realTime="realTime"
            @imgLoad="imgLoad">
        </vue-cropper>
      </div>
      <!---->
      <div class="footer-btn">
        <div class="scope-btn">
          <label class="btn" for="uploads"></label>
          <input type="file" id="uploads" style="position:absolute; clip:rect(0 0 0 0);" accept="image/png, image/jpeg, image/gif, image/jpg" @change="selectImg($event)">
          <el-button size="mini" type="danger" plain icon="el-icon-zoom-in" @click="changeScale(1)"></el-button>
          <el-button size="mini" type="danger" plain icon="el-icon-zoom-out" @click="changeScale(-1)"></el-button>
          <el-button size="mini" type="danger" plain @click="rotateLeft"> </el-button>
          <el-button size="mini" type="danger" plain @click="rotateRight"> </el-button>
        </div>
        <div class="upload-btn">
          <el-button size="mini" type="success" @click="uploadImg('blob')"> <i class="el-icon-upload"></i></el-button>
        </div>
      </div>
    </div>
    <!---->
    <div class="show-preview">
      <div :style="previews.div" class="preview">
        <img :src="previews.url" :style="previews.img">
      </div>
    </div>
  </div>
</template>

<script>
import { VueCropper } from 'vue-cropper'
export default {
  name: "CropperImage",
  components: {
    VueCropper
  },
  props:['Name'],
  data() {
    return {
      name:this.Name,
      previews: {},
      option:{
        img: '',             //
        outputSize: 1,       //(0.1 - 1)
        outputType: 'jpeg',  //jpeg || png || webp
        info: true,          //
        canScale: true,      //
        autoCrop: true,      //
        autoCropWidth: 230,  //
        autoCropHeight: 150, //
        fixed: false,         //
        fixedNumber: [1.53, 1], //
        full: false,         //false
        fixedBox: false,      //
        canMove: false,      //
        canMoveBox: true,    //
        original: false,     //
        centerBox: false,    //
        height: true,        //dpr 
        infoTrue: false,     //truefalse
        maxImgSize: 3000,    //
        enlarge: 1,          //
        mode: '230px 150px'  //
      }
    };
  },
  methods: {
    //
    imgLoad (msg) {
      console.log("====="+msg)
    },
    //
    changeScale (num) {
      num = num || 1
      this.$refs.cropper.changeScale(num)
    },
    //
    rotateLeft () {
      this.$refs.cropper.rotateLeft()
    },
    //
    rotateRight () {
      this.$refs.cropper.rotateRight()
    },
    //
    realTime (data) {
      this.previews = data
    },
    //
    selectImg (e) {
      let file = e.target.files[0]
      if (!/\.(jpg|jpeg|png|JPG|PNG)$/.test(e.target.value)) {
        this.$message({
          message: 'jpegjpgpng',
          type: "error"
        });
        return false
      }
      //blob
      let reader = new FileReader()
      reader.onload = (e) => {
        let data
        if (typeof e.target.result === 'object') {
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      //base64
      reader.readAsDataURL(file)
    },
    //
    uploadImg (type) {
      let _this = this;
      if (type === 'blob') {
        //blob
        this.$refs.cropper.getCropBlob(async (data) => {
          let formData = new FormData();
          formData.append('file',data,"DX.jpg")
          //axios
          let {data: res} = await _this.$http.post('/api/file/imgUpload', formData)
          if(res.code === 200){
            _this.$message({
              message: res.msg,
              type: "success"
            });
            let data = res.data.replace('[','').replace(']','').split(',');
            let imgInfo = {
              name : _this.Name,
              url : data[0]
            };
            _this.$emit('uploadImgSuccess',imgInfo);
          }else {
            _this.$message({
              message: '',
              type: "error"
            });
          }
        })
      }
    },
  },
}
</script>

